.food-entry-add-view {
    height: 100%;

}

.food-entry-add-view__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 25px;
}

.food-entry-add-view__input {
    width: 100%;
    font-size: 20px;
    font-family: "Inter";
    border: 2px solid #E8EAEF;
    background-color: #F3F5FA;
    padding: 10px 15px;
    text-align: center;
    height: 60px;
}

.food-entry-add-view__input:first-child {
    border-bottom-width: 0;
}

.food-entry-add-view__button {
    width: 100%;
    height: 60px;
    background-color: #F3F5FB;
    transition: background-color .2s ease;
    color: #647080;
    border: none;
    outline: none;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #e8eaef;
    border-top: 0;
}

.food-entry-add-view__button:active {
    background-color: #e4e6ec;
}

.food-entry-add-view__buttons {
    display: flex;
    width: 100%;
}
