.food-entries-view {
    height: 100%;
}

.food-entries-view__food-entries-container {
    height: 100%;
}

.food-entries {
    height: 100%;
}

.food-entries__header-container {
    background-color: #F3F5FC;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: 500;
    color: #617082;
}

.food-entries__food-entries-container {
    border: 2px solid #F2F5FC;
    height: calc(100% - 60px);
    overflow: scroll;
}

:root {
    --food-entry--height: 60px;
    --food-entry--base-font-size: 18px;
    --food-entry--border-color: #F2F5FC;
}

.food-entry {
    user-select: none;
    height: var(--food-entry--height);
    border-bottom: 2px solid var(--food-entry--border-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #617082;
    transition: background-color .2s ease;
    overflow: hidden;
    font-size: calc(var(--food-entry--base-font-size) * 1);
}

.food-entry:last-child {
    border-bottom: none;
}

.food-entry:active {
    background-color: #E7EBF5;
}

.food-entry__description {
    padding: 0 15px;
    font-weight: 400;
    font-style: italic;
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
}

.food-entry__time-stamp {
    text-align: center;
    width: var(--food-entry--height);
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(var(--food-entry--base-font-size) * .8);
    border-left: 2px solid var(--food-entry--border-color);
}

.food-entry__time-stamp-time {
    font-weight: 500;
}

.food-entry__time-stamp-ampm {
    font-size: calc(var(--food-entry--base-font-size) * .6);
    font-weight: 600;
}

.food-entry__calories {
    width: var(--food-entry--height);
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: calc(var(--food-entry--base-font-size) * .9);
    font-weight: 500;
    font-style: italic;
    border-right: 2px solid var(--food-entry--border-color);
}
