.nav {
    height: 100%;
}

.nav__buttons-container {
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.nav__button {
    flex: 1;
    height: 100%;
    font-size: 26px;
    background-color: #F3F5FB;
    color: #647080;
    border: none;
    outline: none;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color .2s ease;
    border: 2px solid #e8eaef;
}

.nav__button:first-child {
    border-right: 0;
}

.nav__button:active {
    background-color: #e8eaef;
}
