.dialog-box {
    position: absolute;
    left: 0; right: 0; top: 0; bottom: 0;
    background-color: #FFFFFF;
    max-width: 600px;
    margin: auto;
    border: 2px solid #eee;
}

@keyframes fade-opacity {
    from { opacity: 0; } to { opacity: 1; }
}

.dialog-box--opening {
    animation: fade-opacity 200ms ease forwards;
}

.dialog-box--closing {
    animation: fade-opacity 200ms ease reverse forwards;
}

.dialog-box--opened {
    opacity: 1;
}

.dialog-box--closed {
    opacity: 0;
}

.dialog-box__header {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    background-color: #F3F5FB;
}

.dialog-box__button {
    background-color: #F3F5FB;
    color: #647080;
    border: none;
    outline: none;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color .2s ease;
    border: 2px solid #e8eaef;
    height: 60px;
    width: 60px;
}

.dialog-box__button:active {
    background-color: #e8eaef;
}

.dialog-box__content {
    height: calc(100dvh - 60px);
}
